@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Red Hat Display', sans-serif;
	background: linear-gradient(to right, rgb(25, 28, 31), rgb(52, 45, 45));
}

.header {
	display: flex;
	/* flex-direction: column; */
	justify-content: space-between;
	align-items: center;
	background-color: #1a1a1a;
	padding: 2rem 10rem;
}

.header h1 {
	color: #f5c518;
	margin-bottom: 1rem;
	font-size: 3rem;
}

.title {
  width: 100%;
}

.featured {
  width: 350px;
}

.featured h2 {
	color: #111;
	width: 100%;
  text-align: center;
	border-radius: 50px;
	padding: 0.5rem 2rem;
	font-size: 1rem;
	cursor: pointer;
	background-color: #f5c518;
	transition: all 0.5s ease;
}

.featured h2:hover {
	background-color: #f5c518ab;
}

input {
	width: 350px;
	height: 50px;
	padding: 1rem;
	text-align: center;
	background-color: #fff;
	border-radius: 50px;
	font-size: 1.2rem;
}

input:focus {
	outline: none;
}

input:focus::-webkit-input-placeholder {
	color: transparent;
}

input:focus:-ms-input-placeholder {
	color: transparent;
}

input:focus::placeholder {
	color: transparent;
}


@media (max-width: 1200px) {
  .header {
     flex-direction: column;
     padding: 2rem 2rem;
}

.header h1 {
    font-size: 3rem;
}

.title {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem 0rem;
}
}

@media (max-width: 1200px) {
  .header h1 {
font-size: 2.2rem;
}
.featured, input {
  width: 300px;
}
}


.movie {
	width: 300px;
	margin: 1rem;
	background-color: #1a1a1a;
	position: relative;
	transition: 0.2s all ease;
	overflow: hidden;
}

.movie-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.movie img {
	width: 100%;
	height: 450px;
	transition: 0.2s all ease;
}

.movie:hover.movie img {
	-webkit-filter: brightness(50%);
	        filter: brightness(50%);
}

.movie-info {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin: 1rem 2rem;
	color: #fff;
}

.movie-info span {
	color: #111;
	font-size: 0.5rem;
	font-weight: bold;
	background-color: #f5c518;
	padding: 0.3rem 0.5rem;
	border-radius: 10px;
	border: 1px solid #111;
display:none;
}

.movie-release {
	display: flex;
	align-items: center;
	margin: 1rem 2rem;
	font-size: 0.7rem;
	color: #fff;
}

.movie-overview h3 {
	font-size: 1.5rem;
	margin-bottom: 0.5rem;
}

.movie-overview {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-top: 3px solid #111;
	position: absolute;
	bottom: 0;
	background-color: #f5c518;
	padding: 1.5rem;
	font-size: 0.8rem;
	transform: translateY(100%);
	transition: transform 0.5s ease;
}

.movie:hover .movie-overview {
	transform: translateY(0%);
}

